@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&family=Source+Serif+Pro:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  min-height: 100vh;
  /*background: #F2EFEB;*/
  /*background: #F2F2F2;*/
  /*background:#F8FBF8;*/
  --titleFont: 'Nanum Myeongjo', serif;
  --mainFont: 'Raleway', sans-serif;
  font-family: 'Raleway', sans-serif;
  --fblack:#050203;
  /*--fgrey: #A6A297;*/
  --fgrey: #777A88;

  background: #E7E5EA60;
  padding: 0 16vw;


}

.linkStyle {
  text-decoration: none;
  color: inherit;
  font-style: normal;
  letter-spacing: 0.02rem;
}
.linkStyle:hover {
  cursor: pointer;
  -webkit-transform: translateY(-2px);
  transform: translateY(-2px);
  color: var(--fblack);
  border: None;
  transition: all 0.25s ease-out;
}

@media (max-width: 1600px) {
  .App {
    padding: 0 10%;
  }
}
@media (max-width: 900px) {
  .App {
    padding: 0 5%;
  }
}



.headbar{
    display:flex;
    flex-direction: column;
    align-items: center;
    color:var(--fblack);
    font-family: var(--titleFont);
    font-weight: bold;
}

.title{
    width: 100%;
    font-size: 48pt;
    letter-spacing: 2px;
    margin-top:16px;
    text-align: left;
}
.chosen{
    font-size:15pt;
    text-decoration:none;
}
.notChosen{
    text-decoration: none;
    color: #96959F;
    font-size:14pt;
    font-weight: 800;

}
.bar{
    width:100%;
    border-style:solid;
    border-width: 0px 0 1px 0;
    display: inline-flex;
    flex-direction: row;
    padding: 2px 0;
    justify-content: flex-end;


}
.bar > *{
    padding: 0 0 4px 64px;
    letter-spacing: 1px;


}
/************************************* Main Page **************************************************************/

.main_container{
    display: flex;
    padding: 100px 0;
    flex-direction: column;
    /*flex-wrap:wrap;*/
}
.main_container >*{
    display: flex;
    margin-bottom: 100px;
}
.item:nth-child(odd){
    flex-direction: row
}
.item:nth-child(even){
    flex-direction: row-reverse;
}
.item >*{
    width: 50%;
}
.main_container ul{
    /*background: green;*/
    padding: 2px 2px;
    text-align: justify;
    list-style: None;
    font-weight: bold;
    font-size: 16pt;
}

.main_container li{
    margin: 8px 0 0 0;
    /*line-height: 18pt;*/
    font-weight: 400;
    font-size: 14pt;

}
.br{
    font-family: var(--titleFont);
    font-size: 32pt;
    letter-spacing: 1px;
    /*text-align: left;*/
    font-weight: bold;
    margin: 8px 0;
}
.double_img{
    width: 50%;
}

.img img{
  position: relative;
    margin:56px 4% 40px 4%;
  width: 92%;
  min-width: 240px;
  /* margin-left: 15vw; */

}
.doubleimg img{
    position: relative;
    margin:36px 10% 4px 10%;
    width: 80%;
    min-width: 240px;
}

.des{
    width:45%;
    padding: 0 2.5%;
    /*margin:0px auto 24px auto;*/
}
 .double_img img{
     position: relative;
  width: 40%;
     margin: 16px 16px 0 16px;
  min-width: 100px;
 }

.bomb img{
    width: 40%;
}

.route img{
    width: 60%;
}

 @media (max-width: 1500px) {
  .title {
      font-size: 40pt;
  }
}
 @media (max-width: 1000px) {
    .br{
        font-size: 26pt;
    }
     .item:nth-child(1n){
         flex-direction: column;
    }
     .item >*{
         width: 100%;
         margin-bottom: 60px;
     }
}

 @media (max-width: 800px) {
  .title {
      font-size: 24pt;
      text-align: center;
  }
     .main_container{
         padding: 60px 0;
     }
     .item >*{
         width:100%
     }

     .bar{
    justify-content: center;
}
     .bar > *{
    font-size: 16px;
    padding: 4px 12px;

}
     .des{
         width: 90%;
         margin:12px auto;

     }
     .img img{
         margin:12px auto;

     }

}
@media (max-width: 500px){
    .title {
      font-size: 20pt;
      top:0;
      text-align: center;
  }
    .br{
        font-size: 24pt;
    }

.bar > *{
    font-size: 12px;
    padding: 4px 12px;

}
}
@media (max-width: 250px){
    .title {
      font-size: 20pt;
      top:0;
      text-align: center;
  }
    .br{
        font-size: 20pt;
    }

.bar > *{
    font-size: 10px;
    padding: 4px 12px;

}
}



 /*============================================================ Tree =================================================*/
 .treeWrapper{
     width:80vw;
     height :100vh;

 }
 .tree{
     margin: 0 10vw;
     background: #A6A297;
 }
 .ttButtons{
     margin: 60px 0;
     display: flex;
     justify-content: space-around;
 }
 .ttButtons button{
     border-radius: 15px;
     font-size: 18px;
     padding: 6px 8px;
     border-style: solid;
     border-width: 2px;
     border-color: var(--fgrey);
     color:var(--fblack);
     font-family: var(--mainFont);

     background: None;
 }
 .ttButtons button:hover{
  cursor: pointer;
  -webkit-transform: translateY(-2px);
  transform: translateY(-2px);
  color: var(--fblack);
  border-color: var(--fblack);
  transition: all 0.25s ease-out;
}

 /* custom-tree.css */

.node__done > circle {
  fill: #EBC85C;
  fill: #FFAA39;
    stroke:None;
    r: 14;

}
.node__done > text{
    stroke: var(--fgrey);
    fill: var(--fgrey);
    stroke-width:1;
    font-size: 18px;
}
.custom_link {
    stroke:#777A8890 !important;
    stroke-width: 2 !important;
}

.node__branch > circle {
  fill: #1455C2;
  /*fill: #BFE4E3;*/
    stroke: None;
    r:12;
}

.node__leaf > circle {
  fill: #C8DBEE;
  stroke: None;
  /* Let's also make the radius of leaf nodes larger */
}

.node__branch>text{
    stroke:var(--fblack);
    fill:var(--fblack);
    stroke-width:1;
    font-size: 22px;


}

@media (max-width: 700px) {
  .ttButtons{
      flex-direction: column;
  }
    .ttButtons>*{
        margin:10px
    }

    .node__done >text{
        font-size: 12px;
    }
    .node__branch > text{
        font-size: 14px;
    }
}


